"use client";

import React from "react";
import { X } from "lucide-react";
import { AnimatePresence, motion } from "framer-motion";

interface ReceiptItem {
    id: number;
    name: string;
    price: number;
    quantity: number;
    seller: string;
    sellerVerified: boolean;
    image?: string;
}

interface OrderReceiptModalProps {
    isOpen: boolean;
    onClose: () => void;
}

const OrderReceiptModal: React.FC<OrderReceiptModalProps> = ({
    isOpen,
    onClose,
}) => {
    const orderData = {
        orderId: "#024588",
        orderTime: "Wednesday 1st January 2026",
        itemsTotal: 48000.0,
        shipping: 8000.0,
        orderTotal: 56000.0,
        shippingAddress: {
            name: "Otor John Stephen",
            phone: "+234 8134756623",
            address:
                "38 Asheik Jarma Street, Abuja, Federal Capital Territory\nNigeria",
        },
        paymentMethod: {
            type: "Master card",
            last4: "XXXX 9682",
            paidOn: "Wednesday 1st January 2026",
            amount: 56000.0,
        },
    };

    const items: ReceiptItem[] = [
        {
            id: 1,
            name: "The name of the product",
            price: 50000.0,
            quantity: 2,
            seller: "Offizy Stores",
            sellerVerified: true,
        },
        {
            id: 2,
            name: "The name of the product",
            price: 50000.0,
            quantity: 2,
            seller: "Offizy Stores",
            sellerVerified: true,
        },
    ];

    const formatCurrency = (amount: number) => {
        return `₦${amount.toLocaleString("en-NG", {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2,
        })}`;
    };

    return (
        <AnimatePresence>
            {isOpen && (
                <>
                    {/* Backdrop */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                        className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 cursor-pointer"
                    />

                    {/* Sidebar */}
                    <motion.div
                        initial={{ x: '100%' }}
                        animate={{ x: 0 }}
                        exit={{ x: '100%' }}
                        transition={{ type: 'spring', damping: 25, stiffness: 200 }}
                        className="fixed right-0 top-0 h-full w-full max-w-[700px] bg-white shadow-2xl z-[60] overflow-y-auto"
                    >
                        {/* Header */}
                        <div className="sticky top-0 bg-white flex items-center justify-between p-6 border-b border-gray-200 z-10">
                            <h2 className="text-2xl font-semibold text-gray-900">Order Receipt</h2>
                            <button
                                onClick={onClose}
                                className="w-10 h-10 flex items-center justify-center text-gray-500 hover:text-gray-700 transition-colors cursor-pointer"
                            >
                                <X size={24} />
                            </button>
                        </div>

                        {/* Content */}
                        <div className="p-6 space-y-6 pb-24">
                            {/* Logo and Title */}
                            <div className="flex items-center justify-between">
                                <div className="flex items-center gap-2">
                                    <div className="w-10 h-10 bg-yellow-400 rounded flex items-center justify-center">

                                    </div>
                                    <span className="font-bold text-sm">FLEZTA</span>
                                </div>
                                <span className="text-sm text-gray-600">Order Receipt</span>
                            </div>

                            {/* Order Summary */}
                            <div className="bg-gray-50 rounded-2xl p-4">
                                <h3 className="font-semibold text-gray-900 mb-3">Order summary</h3>
                                <div className="space-y-3 text-sm">
                                    {[
                                        { label: "Order ID", value: orderData.orderId },
                                        { label: "Order time", value: orderData.orderTime },
                                        { label: "Item(s) total", value: formatCurrency(orderData.itemsTotal) },
                                        { label: "Shipping", value: formatCurrency(orderData.shipping) }
                                    ].map((row, i) => (
                                        <div key={i} className="flex justify-between">
                                            <span className="text-gray-600">{row.label}</span>
                                            <span className="text-gray-900 font-medium">{row.value}</span>
                                        </div>
                                    ))}
                                    <div className="flex justify-between pt-3 border-t border-gray-200">
                                        <span className="text-gray-900 font-bold text-lg">Order total</span>
                                        <span className="text-gray-900 font-bold text-lg">
                                            {formatCurrency(orderData.orderTotal)}
                                        </span>
                                    </div>
                                </div>
                            </div>

                            {/* Shipping Address */}
                            <div>
                                <h3 className="font-semibold text-gray-900 mb-3">
                                    Shipping address
                                </h3>
                                <div className="text-sm text-gray-900 bg-gray-50 rounded-2xl p-4 space-y-1 border border-gray-100">
                                    <p className="font-semibold">{orderData.shippingAddress.name}</p>
                                    <p className="text-gray-600">{orderData.shippingAddress.phone}</p>
                                    <p className="whitespace-pre-line text-gray-600">
                                        {orderData.shippingAddress.address}
                                    </p>
                                </div>
                            </div>

                            {/* Payment Method */}
                            <div>
                                <h3 className="font-semibold text-gray-900 mb-3">Payment method</h3>
                                <div className="bg-[#EBF7F4] border border-[#6CF5CA] rounded-xl p-4 mb-4">
                                    <p className="text-xs text-[#077D58] font-bold mb-1">
                                        FLEZTA is committed to protecting your payment information
                                    </p>
                                    <p className="text-xs text-[#077D58]">
                                        We use strong encryption and perform regular reviews of its
                                        system to protect your privacy
                                    </p>
                                </div>
                                <div className="flex items-center justify-between bg-gray-50 rounded-2xl p-4 border border-gray-100">
                                    <div className="flex items-center gap-3">
                                        <div className="w-12 h-8 bg-white border border-gray-200 rounded-lg flex items-center justify-center p-1">
                                            <div className="flex gap-[-4px]">
                                                <div className="w-4 h-4 bg-red-500 rounded-full opacity-80"></div>
                                                <div className="w-4 h-4 bg-yellow-500 rounded-full opacity-80 ml-[-8px]"></div>
                                            </div>
                                        </div>
                                        <div>
                                            <p className="text-sm font-semibold text-gray-900">
                                                {orderData.paymentMethod.type} • {orderData.paymentMethod.last4}
                                            </p>
                                            <p className="text-xs text-gray-500">
                                                Paid on {orderData.paymentMethod.paidOn}
                                            </p>
                                        </div>
                                    </div>
                                    <span className="text-lg font-bold text-gray-900">
                                        {formatCurrency(orderData.paymentMethod.amount)}
                                    </span>
                                </div>
                            </div>

                            {/* Item Details */}
                            <div>
                                <h3 className="font-semibold text-gray-900 mb-3">
                                    Item details ({items.length})
                                </h3>
                                <div className="space-y-4">
                                    {items.map((item) => (
                                        <div key={item.id} className="flex gap-4 bg-white border border-gray-100 rounded-2xl p-3 shadow-sm">
                                            <div className="w-20 h-20 bg-gray-100 rounded-xl flex-shrink-0"></div>
                                            <div className="flex-1 min-w-0">
                                                <p className="text-sm font-semibold text-gray-900 mb-2 truncate">
                                                    {item.name}
                                                </p>
                                                <div className="flex items-center gap-2 text-xs text-gray-600">
                                                    <span>By:</span>
                                                    <div className="w-5 h-5 bg-purple-100 text-purple-700 rounded-full flex items-center justify-center font-bold text-[10px]">
                                                        OS
                                                    </div>
                                                    <span className="font-medium">{item.seller}</span>
                                                    {item.sellerVerified && (
                                                        <span className="text-blue-500">✓</span>
                                                    )}
                                                </div>
                                            </div>
                                            <div className="text-right flex flex-col justify-between py-1">
                                                <p className="text-sm font-bold text-gray-900">
                                                    {formatCurrency(item.price)}
                                                </p>
                                                <p className="text-xs text-gray-500 font-medium">x{item.quantity}</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>

                            {/* Environmental Notice */}
                            <div className="bg-gray-50 rounded-2xl p-4 border border-dashed border-gray-300">
                                <p className="text-xs text-gray-600 leading-relaxed text-center">
                                    To prevent excess packaging waste, we do not include paper
                                    receipts. However, you can always print one out for your records.
                                </p>
                            </div>

                            {/* Action Buttons */}
                            <div className="fixed bottom-0 right-0 w-full max-w-[500px] bg-white border-t border-gray-100 p-6 flex gap-4 z-20">
                                <button className="flex-1 bg-[#003625] hover:bg-[#00281b] text-white py-4 rounded-[14px] font-semibold transition-all duration-300 shadow-lg shadow-emerald-900/10 cursor-pointer">
                                    Download Receipt
                                </button>
                                <button className="flex-1 border-2 border-[#FDA106] text-[#FDA106] hover:bg-[#FFF4EA] py-4 rounded-[14px] font-semibold transition-all duration-300 cursor-pointer">
                                    Share Receipt
                                </button>
                            </div>
                        </div>
                    </motion.div>
                </>
            )}
        </AnimatePresence>
    );
};

export default OrderReceiptModal;